const BackerSign: React.FC<Props> = (props) => (
  <svg
    viewBox="0 0 40 40"
    xmlns="http://www.w3.org/2000/svg"
    className={`${props.hoverAnimation ? styles.hoverAnimation : ""}`}
  >
    <BackerSignPath />
  </svg>
);

const renderLogo = (props: Props) => {
  switch (props.type) {
    case "default":
      return <Backer hoverAnimation={props.hoverAnimation} />;
    case "foundation":
      return <BackerFoundation hoverAnimation={props.hoverAnimation} />;
    case "sign":
      return <BackerSign hoverAnimation={props.hoverAnimation} />;
  }
};

const Logo = React.forwardRef<any, Props>((props, ref) => {
  return (
    <div ref={ref} className={`${props.className} ${styles.logo}`}>
      {renderLogo(props)}
    </div>
  );
});

Logo.displayName = "Logo";

Logo.defaultProps = {
  className: "",
  hoverAnimation: true,
  type: "default",
} as Partial<Props>;

export default Logo;
